* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a,
a:hover {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
}

button {
  cursor: pointer;
  outline: none;
}

input,
textarea {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}

.container-custom {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}

body {
  background: #fff;
}

.block_bg {
  width: 520px;
  height: 580px;
  background: white;
  background: -o-linear-gradient(45deg, white 10%, #338562 50%, #007744 100%);
  background: linear-gradient(45deg, white 10%, #338562 50%, #007744 100%);
  border-radius: 30px;
  position: absolute;
  top: -250px;
  left: 100px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.header {
  padding: 2rem 0 2rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.header .title {
  font-family: 'Roboto';
  font-size: 24px;
  font-weight: 500;
  color: #fff;
  padding: 5px;
}

.header .title .fa {
  font-size: 21px;
  margin-right: 7px;
}

.header .lang {
  width: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.header .lang__item {
  padding: 5px;
}

.header .lang__item .img {
  width: 20px;
  height: 20px;
}

.menu__block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin: 0 !important;
}

.menu__block .item {
  width: 200px;
  font-family: 'Roboto';
  font-size: 17px;
  font-weight: 400;
  color: #263128;
  text-align: center;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  border-radius: 10px;
  padding: 1rem;
  background: rgba(177, 177, 177, 0.3);
  -webkit-box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  margin-bottom: 2rem;
}

.menu__block .item:nth-last-child(1) {
  margin-left: 15px;
}

.section .block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.section .block__item {
  width: 180px;
  height: 190px;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  border-radius: 10px;
  padding: 1rem;
  background: rgba(177, 177, 177, 0.3);
  -webkit-box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin-bottom: 2rem;
}

.section .block__icon {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: #fff;
}

.section .block__icon .fa {
  font-size: 2.8rem;
  color: #007744;
}

.section .block__title {
  text-align: center;
  font-family: 'Roboto';
  font-size: 17px;
  font-weight: 400;
  color: #263128;
}

.section-search {
  position: relative;
}

.section-search .search {
  width: calc(100% - 10px);
  background: #fff;
  margin: 5px;
}

.section-search .search .fa {
  font-size: 18px;
  padding: 10px;
  color: #666666;
  background: #fff;
  margin: 0;
}

.section-search .search__input {
  width: 94.5%;
  font-family: 'Roboto';
  font-size: 18px;
  color: #263128;
  border: none;
  margin: 0;
  padding: 10px;
}

.section-search .lists {
  padding: 0 5px;
}

.section-search .lists__item {
  width: 100%;
  -webkit-backdrop-filter: blur(40px);
  backdrop-filter: blur(40px);
  border-radius: 10px;
  padding: 1rem;
  background: rgba(177, 177, 177, 0.3);
  -webkit-box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  box-shadow: rgba(24, 32, 79, 0.25) 0px 10px 25px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.section-search .lists__title {
  font-family: 'Roboto';
  font-size: 16px;
  color: #263128;
}

.modal_bg {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  background: rgba(75, 75, 75, 0.671);
}

.history__title {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #263128;
  padding: 10px 50px 10px 15px;
  background: #fff;
  -webkit-box-shadow: rgba(24, 32, 79, 0.2) 0px 10px 25px;
  box-shadow: rgba(24, 32, 79, 0.2) 0px 10px 25px;
  position: relative;
  z-index: 2;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.history__list {
  background: #f1f1f1;
  margin: 0 10px;
  padding: 2rem 1rem 1rem 1rem;
  margin-bottom: 15px;
  position: relative;
  z-index: 1;
}

.history__list p {
  font-family: 'Roboto';
  font-size: 15px;
  font-weight: 400;
  color: #263128;
}

#map {
  border-radius: 24px;
  width: 100%;
  height: calc(100vh - 150px);
}

.title-br {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #263128;
  text-align: center;
  padding: 10px 0;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background: #fff;
  -webkit-box-shadow: rgba(24, 32, 79, 0.2) 0px 10px 25px;
  box-shadow: rgba(24, 32, 79, 0.2) 0px 10px 25px;
  position: relative;
  z-index: 2;
}

.booking-title {
  background: #f1f1f1;
  padding: 0.5rem 1rem 0.5rem;
  margin: 0 10px 0px;
  position: relative;
  z-index: 1;
  text-align: center;
}

.booking-title h2 {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 500;
  color: #263128;
}

.booking-title p {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 500;
  color: #263128;
}

.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  background: #fff;
  -webkit-box-shadow: rgba(24, 32, 79, 0.2) 0px -10px 25px;
  box-shadow: rgba(24, 32, 79, 0.2) 0px -10px 25px;
  position: relative;
  z-index: 2;
  padding: 1rem 0.5rem 1.5rem 0.5rem;
  margin-bottom: 15px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  margin-bottom: 15px;
}

.card__item {
  margin-bottom: 5px;
}

.card__item p {
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 500;
  color: #263128;
  padding-top: 1rem;
  text-align: center;
}

.card__item .input {
  width: 100%;
  font-family: 'Roboto';
  font-size: 16px;
  color: #263128;
  border: none;
  padding: 10px;
  border-bottom: 1px solid #DEDEDE;
  margin-bottom: 10px;
}

.card .btn {
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 400;
  background: #007744;
  color: #fff;
  border: none;
  padding: 8px 10px;
  margin-top: 10px;
  border-radius: 5px;
}

.card-modal {
  display: none;
  width: 300px;
  top: 30%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  position: absolute;
  z-index: 4;
  border-radius: 10px;
  padding: 0 1rem 1rem 1rem;
  background: white;
  padding-top: 30px;
}

.card-modal .close-link {
  position: absolute;
  top: 0px;
  right: 5px;
}

.card-modal .close-link .fa {
  font-size: 24px;
  color: red;
  padding: 10px;
}

.card-modal h1,
.card-modal h2 {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 500;
  color: #838383;
  margin-bottom: 5px;
}

.card-modal p {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #263128;
  margin-bottom: 10px;
}

.card-modal .btn {
  width: 100%;
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 400;
  background: #007744;
  color: #fff;
  border: none;
  padding: 8px 10px;
  margin-top: 10px;
  border-radius: 5px;
}

.card-modal-end {
  display: none;
  width: 700px;
  top: 30%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  position: absolute;
  z-index: 4;
  border-radius: 10px;
  padding: 0 1rem 1rem 1rem;
  background: white;
  padding-top: 30px;
}

.card-modal-end .close-link {
  position: absolute;
  top: 0px;
  right: 5px;
}

.card-modal-end .close-link .fa {
  font-size: 24px;
  color: red;
  padding: 10px;
}

.card-modal-end h1 {
  font-family: 'Roboto';
  font-size: 24px;
  font-weight: 500;
  color: #263128;
}

.card-modal-end .kod {
  border: 1px solid #dfdfdf;
  border-radius: 10px;
  position: relative;
  margin-top: 25px;
  -webkit-transition: .3s all ease-in-out;
  -o-transition: .3s all ease-in-out;
  transition: .3s all ease-in-out;
}

.card-modal-end .kod:hover {
  border-color: #000;
}

.card-modal-end .kod__title {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  color: #b4b4b4;
  position: absolute;
  top: -8px;
  left: 15px;
  background: #fff;
  padding: 0 5px;
  -webkit-transition: .3s all ease-in-out;
  -o-transition: .3s all ease-in-out;
  transition: .3s all ease-in-out;
}

.card-modal-end .kod__number {
  font-family: 'Roboto';
  font-size: 24px;
  font-weight: 400;
  color: #b4b4b4;
  margin-top: 25px;
  padding: 0 0 15px 25px;
  -webkit-transition: .3s all ease-in-out;
  -o-transition: .3s all ease-in-out;
  transition: .3s all ease-in-out;
}

.card-modal-end .kod:hover .kod__title {
  color: #263128;
}

.card-modal-end .kod:hover .kod__number {
  color: #263128;
}

.card-modal-end h2 {
  font-family: 'Roboto';
  font-size: 20px;
  font-weight: 400;
  color: #8b8b8b;
  padding: 15px 0 15px 20px;
}

.card-modal-end p {
  font-family: 'Roboto';
  font-size: 12px;
  font-weight: 400;
  color: #8b8b8b;
}

.card-modal-end h3 {
  font-family: 'Roboto';
  font-size: 15px;
  font-weight: 400;
  color: #8b8b8b;
  margin-top: 10px;
}

.active {
  display: block;
}

